<template>
  <div id="new-thing">
    <v-layout row>
      <v-flex xs12 sm8 offset-sm2>
        <v-form ref="form">
            <!-- <v-text-field
              label="ID#"
              v-model="thing_id"
            ></v-text-field> -->
            <v-text-field
              label="Name"
              v-model="name"
              :counter="30"
            ></v-text-field>
            <v-text-field
              label="Category"
              v-model="category"
            ></v-text-field>
            <v-text-field
              label="Location"
              v-model="location"
            ></v-text-field>
            <v-text-field
              label="Description"
              v-model="description"
            ></v-text-field>
            <v-text-field
              label="Image"
              v-model="image"
            ></v-text-field>
            <v-btn color="success"
              @click="saveThing"
            >
              submit
            </v-btn>
            <router-link to="/" class="card-btn">  
              <v-btn outline color="primary">Back</v-btn>
            </router-link>
          </v-form>
        </v-flex>
      </v-layout>  
  </div>
</template>

<script>
import db from './firebaseInit'
export default {
  name: 'new-thing',
  data () {
    return {
      thing_id: null,
      name: null,
      description: null,
      location: null,
      image: null,
      category: null
    }
  },
  methods: {
    saveThing () {
      db.collection('things').add({
        thing_id: this.generateUUID(),
        name: this.name,
        description: this.description,
        location: this.location,
        image: this.image,
        category: this.category
      })
      .then(docRef => this.$router.push('/'))
      .catch(error => console.log(err))
    },
    generateUUID () {
      let d = new Date().getTime()
      let uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        let r = (d + Math.random() * 16) % 16 | 0
        d = Math.floor(d / 16)
        return (c === 'x' ? r : (r & 0x3 | 0x8)).toString(16)
      })
      return uuid
    }
  }
}
</script>

<style scoped>

a{
  text-decoration: none;
}

</style>




